<template>
  <div>
    <van-cell class="comment-item">
      <van-image
        slot="icon"
        class="avatar"
        round
        fit="cover"
        :src="comItem.aut_photo"
      />
      <div slot="title" class="title-wrap">
        <div class="user-name">{{ comItem.aut_name }}</div>
        <van-button
          class="like-btn"
          :class="{ likeing: comItem.is_liking }"
          icon="good-job"
          @click="likeFn"
        >
          {{ comItem.like_count || "赞" }}
        </van-button>
      </div>

      <div slot="label">
        <p class="comment-content">{{ comItem.content }}</p>
        <div class="bottom-info">
          <span class="comment-pubdate">{{ comItem.pubdate | datatime }}</span>
          <van-button class="reply-btn" round @click="replyComFn">
            回复 {{ comItem.reply_count }}
          </van-button>
        </div>
      </div>
    </van-cell>
  </div>
</template>

<script>
import { likeingGoApi, likeingOutApi } from "@/api/comment";
export default {
  props: {
    comItem: Object,
  },
  name: "CommentItem",
  components: {},
  data() {
    return {};
  },
  methods: {
    async likeFn() {
      try {
        if (this.comItem.is_liking) {
          //走取消点赞接口
          await likeingOutApi(this.comItem.com_id);
          //手动修改视图
          this.comItem.like_count--;
        } else {
          //走点赞接口
          await likeingGoApi(this.comItem.com_id);
          //手动修改视图
          this.comItem.like_count++;
        }
        //修改用户点赞的状态
        this.comItem.is_liking = !this.comItem.is_liking;
      } catch (error) {
        console.log(error);
      }
    },
    replyComFn() {
      //打开评论回复弹层
      // item->list->详情
      this.$emit("openReplyCom", this.comItem);
    },
  },
};
</script>

