<script>
// import { articleApi, followGoApi, followOutApi } from "@/api/article";
import { articleApi } from "@/api/article";
import { ImagePreview } from "vant";
import followCom from "@/components/followCom.vue";
import collectedCom from "@/components/collectedCom.vue";
import likeingCom from "@/components/likeingCom.vue";
import commentPost from "./component/commentPost.vue";
import commentReply from "./component/commentReply.vue";

// 评论列表组件
import commentList from "./component/commentList.vue";
export default {
  name: "ArticleIndex",
  components: {
    followCom,
    collectedCom,
    likeingCom,
    commentList,
    commentPost,
    commentReply,
  },
  props: {
    articleId: {
      type: [Number, String],
      required: true, //必传项
    },
  },
  data() {
    return {
      loadingShow: false,
      articleContent: {}, //存储文章详情数据
      statusShow: false, //资源是否是404，如果是则显示
      following: false, //按钮是否开启loading，默认是false
      totalCount: 0, //评论总数
      show: false, //发布评论弹层的是否打开
      newCommentObj: {}, //后台返回的最新的评论对象
      replyComShow: false, //评论再回复弹层
      topCommentItem: {}, //层主的评论（顶部数据）
    };
  },
  created() {
    this.$store.commit("setArticleId", this.articleId);
    //新的知识点
    //通过props接收路由参数
    // 1、要在路由配置对象内开启props传参
    this.retryFn();
  },
  methods: {
    //点击重试
    async retryFn() {
      try {
        this.loadingShow = true;
        let { data } = await articleApi(this.articleId);
        this.articleContent = data.data;
        this.$nextTick(() => {
          //1、获取文章dom元素
          let divdom = this.$refs.divcontent;
          //2、获取文章内容所有的img图片
          let imglist = divdom.querySelectorAll("img");
          //3、vue里Dom的更新是异步的
          //4、小结：vue里dom的更新是异步的，而且created生命周期真实的dom还未渲染
          //5、img拿到了，该给img绑定点击事件了
          //6、还要拿到所有图片的src属性
          let arr = [];
          imglist.forEach((item, index) => {
            //文章内容所有的图片添加到arr数组内，后面预览会用
            arr.push(item.src);
            //每一个图片绑定点击事件
            item.onclick = function () {
              //调用图片预览的方法
              ImagePreview({
                images: arr, //需要预览的图片 URL 数组
                startPosition: index, //图片预览起始位置索引
              });
            };
          });
          console.log(imglist);
        });
        this.loadingShow = false;
      } catch (error) {
        this.loadingShow = false; //不管是加载成功还是失败，都关闭loading
        if (error.response.status == 404) {
          this.statusShow = true;
        }
        console.log(error);
      }
    },
    isLikeFn(val) {
      this.articleContent.attitude = val;
    },
    addCommentFn() {
      this.show = true;
    },
    //发布评论之后新添加的评论
    newCommentObjFn(obj) {
      this.newCommentObj = obj;
    },

    openReplyCom(item) {
      //打开评论回复弹层
      this.replyComShow = true;
      this.topCommentItem = item;
    },
    // async followFn() {
    //   //点击关注或未关注，开启loading状态
    //   this.following = true;
    //   // true(关注)false(未关注), 说的是当前登录用户对这个文章作者的关注状态
    //   try {
    //     //判断当时是关注还是未关注
    //     //1、关注了走取消关注接口
    //     //2、未关注走关注接口
    //     if (this.articleContent.is_followed) {
    //       //取消关注接口
    //       await followOutApi(this.articleContent.aut_id);
    //     } else {
    //       //关注接口
    //       await followGoApi(this.articleContent.aut_id);
    //     }
    //     //接口请求成功之后，关闭loading状态
    //     this.following = false;
    //     //修改视图的状态
    //     this.articleContent.is_followed = !this.articleContent.is_followed;
    //   } catch (error) {
    //     console.log(error);
    //   }
    // },
  },
};
</script>

