//封装评论相关的接口
import request from "@/utils/request";
//请求评论列表
export const getCommentListApi = (params) =>
  request({
    url: "/v1_0/comments",
    method: "get",
    params,
  });

//点赞评论
export const likeingGoApi = (target) =>
  request({
    url: "/v1_0/comment/likings",
    method: "post",
    data: {
      //评论id/回复id,对文章评论/评论的回复点赞
      target,
    },
  });

//取消点赞评论
export const likeingOutApi = (target) =>
  request({
    url: `/v1_0/comment/likings/${target}`,
    method: "delete",
  });

//发布评论
export const commentPostApi = (data) =>
  request({
    url: "/v1_0/comments",
    method: "post",
    data,
  });
