<template>
  <div>
    <van-button
      class="follow-btn"
      round
      size="small"
      @click="followFn"
      :loading="following"
      v-if="isFollowed"
      >已关注</van-button
    >
    <van-button
      :loading="following"
      class="follow-btn"
      type="info"
      color="#3296fa"
      round
      size="small"
      icon="plus"
      v-else
      @click="followFn"
      >关注</van-button
    >
  </div>
</template>
<script>
import { followGoApi, followOutApi } from "@/api/article";
export default {
  // props: ["isFollowed", "autId"],
  model: {
    prop: "isFollowed",
    event: "isFollowFn",
  },
  props: {
    autId: {
      type: [Number, String],
    },
    isFollowed: Boolean,
  },
  data() {
    return {
      following: false, //按钮是否开启loading，默认是false
    };
  },
  methods: {
    async followFn() {
      //点击关注或未关注，开启loading状态
      this.following = true;
      // true(关注)false(未关注), 说的是当前登录用户对这个文章作者的关注状态
      try {
        //判断当时是关注还是未关注
        //1、关注了走取消关注接口
        //2、未关注走关注接口
        if (this.isFollowed) {
          //取消关注接口
          await followOutApi(this.autId);
        } else {
          //关注接口
          await followGoApi(this.autId);
        }
        //接口请求成功之后，关闭loading状态
        this.following = false;
        //修改视图的状态
        this.$emit("isFollowFn", !this.isFollowed);
        // this.$emit("input", !this.value);
        // this.articleContent.is_followed = !this.articleContent.is_followed;
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>
<style scoped></style>
