<template>
  <div>
    <div class="comment-post">
      <van-field
        class="postcomment"
        v-model="message"
        rows="2"
        autosize
        type="textarea"
        maxlength="50"
        placeholder="请输入留言"
        show-word-limit
      />
      <van-button class="postbtn" @click="postcomment">发布</van-button>
    </div>
  </div>
</template>
<script>
import { commentPostApi } from "@/api/comment";
export default {
  props: {
    artId: {
      type: [Number, String],
    },
  },
  data() {
    return {
      message: "",
    };
  },
  methods: {
    async postcomment() {
      //阻止用户发布空评论
      if (this.message.trim() == 0) {
        this.$toast("请填写内容！！");
        return;
      }
      try {
        let obj = {
          target: this.artId,
          content: this.message,
          art_id: this.$store.state.articleId,
        };
        let { data } = await commentPostApi(obj);
        //发布成功后，晴空输入框的值
        this.message = "";
        this.$emit("newCommentObjFn", data.data.new_obj);
        this.$emit("closepopup");
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>
<style lang="less" scoped>
.comment-post {
  display: flex;
  align-items: center;
  padding: 32px 0 32px 32px;
  .postcomment {
    background-color: #f5f7f9;
  }
  .postbtn {
    width: 150px;
    border: none;
    padding: 0;
    color: #6ba3d8;
    &::before {
      display: none;
    }
  }
}
</style>
