//文章详情页相关的接口

import request from "@/utils/request";
//请求文章详情
export const articleApi = (article_id) =>
  request({
    url: `/v1_0/articles/${article_id}`,
    method: "get",
  });

//关注作者
export const followGoApi = (target) =>
  request({
    url: "/v1_0/user/followings",
    method: "post",
    data: {
      target: target,
    },
  });

//取关作者
export const followOutApi = (target) =>
  request({
    url: `/v1_0/user/followings/${target}`,
    method: "delete",
  });

//收藏文章
export const collectedGoApi = (target) =>
  request({
    url: "/v1_0/article/collections",
    method: "post",
    data: {
      target,
    },
  });

//取消收藏文章
export const collectedOutApi = (target) =>
  request({
    url: `/v1_0/article/collections/${target}`,
    method: "delete",
  });

//文章点赞
export const likeGoApi = (target) =>
  request({
    url: "/v1_0/article/likings",
    method: "post",
    data: { target },
  });

//文章取消点赞
export const likeOutApi = (target) =>
  request({
    url: `/v1_0/article/likings/${target}`,
    method: "delete",
  });
