<template>
  <div>
    <van-button
      class="starbtn"
      :icon="isCollected ? 'star' : 'star-o'"
      type="primary"
      @click="collectFn"
      :loading="collecting"
    >
    </van-button>
  </div>
</template>
<script>
import { collectedGoApi, collectedOutApi } from "@/api/article";
export default {
  model: {
    prop: "isCollected",
    event: "isCollectedFn",
  },
  props: {
    isCollected: Boolean,
    artId: {
      type: [Number, String],
    },
  },
  data() {
    return {
      collecting: false,
    };
  },
  methods: {
    async collectFn() {
      try {
        //点击收藏开启loading效果
        this.collecting = true;
        if (this.isCollected) {
          //取消收藏接口
          await collectedOutApi(this.artId);
          this.$toast.success("取消收藏");
        } else {
          //收藏接口
          await collectedGoApi(this.artId);
          this.$toast.success("收藏成功");
        }
        //接口请求成功关闭loading
        this.collecting = false;
        //修改收藏视图的状态
        this.$emit("isCollectedFn", !this.isCollected);
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>
<style scoped lang="less">
/deep/.starbtn {
  background: #fff;
  border: 0px;
  color: #3a3a3a;
  .van-icon-star-o {
    color: #3a3a3a;
  }
  .van-icon-star {
    color: #ff9912;
  }
}
</style>
