<template>
  <div>
    <van-button
      :icon="isLike == 1 ? 'good-job' : 'good-job-o'"
      type="primary"
      class="starbtn"
      :loading="likeLoading"
      @click="likeFn"
    />
  </div>
</template>
<script>
import { likeGoApi, likeOutApi } from "@/api/article";
export default {
  model: {
    prop: "isLike",
    event: "isLikeFn",
  },
  props: {
    isLike: [Number, String],
    artId: [Number, String],
  },
  data() {
    return {
      likeLoading: false, //喜欢的loading效果
    };
  },
  methods: {
    //点赞未点赞
    async likeFn() {
      try {
        this.likeLoading = true;
        let artid = this.artId;
        if (this.isLike != 1) {
          await likeGoApi(artid);
          this.$emit("isLikeFn", 1);
          this.$toast.success("点赞成功");
        } else {
          await likeOutApi(artid);
          this.$emit("isLikeFn", -1);
          this.$toast.success("取消点赞");
        }

        this.likeLoading = false;
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>
<style lang="less" scoped>
/deep/.starbtn {
  background: #fff;
  border: 0px;
  color: #3a3a3a;
  .van-icon-star-o {
    color: #3a3a3a;
  }
  .van-icon-star {
    color: #ff9912;
  }
}
</style>
