<template>
  <div>
    <!-- 是否在初始化时立即执行滚动位置检查immediate-check默认为true，改为false -->
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      :immediate-check="false"
    >
      <commentItem
        v-for="item in commentList"
        :key="item.com_id"
        :comItem="item"
        @openReplyCom="openReplyCom"
      />
    </van-list>
  </div>
</template>
<script>
import commentItem from "./commentItem.vue";
import { getCommentListApi } from "@/api/comment";
export default {
  props: {
    type: String,
    artId: {
      type: [Number, String],
    },
    newCommentObj: Object,
  },
  components: {
    commentItem,
  },
  data() {
    return {
      commentList: [], //评论列表
      loading: false,
      finished: false,
      offset: "",
    };
  },
  created() {
    this.onLoad();
  },

  methods: {
    // 思考：加载重复数据
    //1、页面触底会触发onLoad
    //2、页面没有铺满一屏 的时候也会触发onLoad
    async onLoad() {
      try {
        let obj = {
          type: this.type,
          source: this.artId,
          offset: this.offset,
          limit: 10,
        };
        let { data } = await getCommentListApi(obj);
        if (data.data.results.length == 0) {
          this.finished = true;
        } else {
          this.offset = data.data.last_id;
          this.commentList = [...this.commentList, ...data.data.results];
          this.loading = false;

          //向父组件传递评论总数
          this.$emit("totalCountFn", data.data.total_count);
        }
      } catch (error) {
        console.log(error);
      }
    },
    openReplyCom(item) {
      this.$emit("openReplyCom", item);
    },
  },

  watch: {
    newCommentObj: {
      deep: true,
      handler(newVal) {
        //最新的评论，插入到列表的头部
        let bool = this.commentList.find((item) => {
          return item.com_id == newVal.com_id;
        });
        //存在的数据不插入，只插入不存在的数据
        if (!bool) {
          this.commentList.unshift(newVal);
        }
      },
    },
  },
};
</script>
<style scoped></style>
